import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Link } from 'react-router-dom';
// import to from 'await-to-js';
// import { getUserSetting } from 'services/userManagement';
import { TableData } from 'components/common';
import { BtnView } from 'components/common/button';
import { connect } from 'react-redux';
import { Card, Row, Input, Form } from 'antd';
import { getAllModule } from 'services/roleModule';

const mapStateToProps = state => ({
  userTypeId: state.app.userInfo.userTypeId,
  roleInfos: state.app.roleInfos,
});
@connect(mapStateToProps)
class ManagementRole extends Component {
  state = {
    // dataTable: [],
    // loading: false,
    // IsPagingEnabled: true,
    // dataTableMenu: [],
    // dataTableUser: [],
  };

  componentDidMount() {
    // this.getListTowntable();
    this.getUserInfo();
  }

  getUserInfo = async () => {
    const dataAdmin = this.props.roleInfos.filter(item => item.isAdmin);
    if (dataAdmin.length === 1) {
      window.location.href = `#/quan-ly-phan-quyen/chinh-sua?id=${dataAdmin[0].moduleId}`;
    }
  };

  confirmDelete = () => {
    // this.setState({
    //   loading: true,
    // });
  };

  handleSaveRowData = () => {};

  cancel = () => {};

  renderTable = () => {
    // const { dataTable } = this.state;
    const columns = [
      {
        title: 'Phân hệ',
        dataIndex: 'name',
        key: 'name',
        editable: true,
        renderFormItemEdit: ({ form, record }) => (
          <Form.Item>
            {form.getFieldDecorator('name', {
              initialValue: record.name,
            })(<Input />)}
          </Form.Item>
        ),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <Link to={`/quan-ly-phan-quyen/chinh-sua?id=${record.id}`}>
              <BtnView />
            </Link>
            {/* <Popconfirm
              title="Bạn chắc chắn muốn xóa?"
              onConfirm={() => this.confirmDelete(record.id)}
              okText="Xóa"
              cancelText="Hủy"
            >
              <BtnDelete />
            </Popconfirm> */}
          </Fragment>
        ),
      },
    ];
    return (
      <TableData
        reload={this.state.reload}
        paramSearch={{}}
        // onRow={record => {
        //   return {
        //     onClick: () => {
        //       this.getRoleInfoModule(record);
        //     }, // click row
        //   };
        // }}
        service={getAllModule}
        columns={columns}
        rowKey="id"
      />
    );
  };

  render() {
    return (
      <Card title="Danh mục tỉnh">
        <Row>{this.renderTable()}</Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ManagementRole />
  </Page>
);



