import { request } from 'utils';

const prefix = '/api/usermanagement';

export const getAllModule = ({ name, page, pageSize, orderBy, isPagingEnabled = false }) =>
  request({
    prefix,
    url: '/Module',
    params: {
      name,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    method: 'GET',
  });

export const getModuleById = id =>
  request({
    prefix,
    url: `/Module/${id}/GetAll`,
    method: 'GET',
  });

export const getMenuByModuleId = (roleId, moduleId) =>
  request({
    prefix,
    url: `/Module/${moduleId}/role/${roleId}/GetAll`,
    method: 'GET',
  });

export const addNewUserInRole = (roleId, moduleId, newIds) =>
  request({
    prefix,
    url: `/Module/${moduleId}/role/${roleId}/user`,
    data: newIds,
    method: 'POST',
  });

export const deleteUser = (roleId, moduleId, id) =>
  request({
    prefix,
    url: `/Module/${moduleId}/role/${roleId}/user`,
    data: [id],
    method: 'DELETE',
  });

export const updateMenu = (roleId, moduleId, id) =>
  request({
    prefix,
    url: `/Module/${moduleId}/role/${roleId}/UpdateMenu`,
    data: [...id],
    method: 'POST',
  });

export const addCreateRole = (moduleId, newRole) =>
  request({
    prefix,
    url: `/Module/${moduleId}/role`,
    data: { moduleId, ...newRole },
    method: 'POST',
  });

export const deleteRole = (moduleId, roleId) =>
  request({
    prefix,
    url: `/Module/${moduleId}/role/${roleId}`,
    method: 'DELETE',
  });

export const updateRole = (moduleId, roleId, editRole) =>
  request({
    prefix,
    url: `/Module/${moduleId}/role/${roleId}`,
    data: { ...editRole },
    method: 'PUT',
  });



